<template>
    <div>
      <b-form @submit.prevent>
        <validation-observer ref="simpleRules">
          <!--  -->
          <b-card
            v-if="showData"
            :title="$route.name === 'SalesOperation-SalesVoucherCreate' ? $t('SalesVoucherModal.createSalesVoucher') : $t('SalesVoucherModal.editSalesVoucher')"
          >
          
            <b-row>
              <!--  -->
              <b-form-group
                :label="$t('SalesVoucherList.salesDate')"
                label-for="salesDate"
                class="col-12 col-sm-4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="salesDate"
                  rules="required"
                >
                  <flat-pickr
                    id="salesDate"
                    v-model="showData.sales_date"
                    class="form-control"
                    :placeholder="$t('SalesVoucherList.salesDate')"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
  
              <!----->
              <b-form-group
                :label="$t('SalesVoucherList.salesNo')"
                label-for="salesNo"
                class="col-12 col-sm-4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="salesNo"
                  rules="required"
                >
                <b-form-input
                    id="salesNo"
                    v-model="showData.seles_order_no"
                    type="text"
                    :placeholder="$t('SalesVoucherList.salesNo')"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
            
  
              <!--  -->
              <b-form-group
                :label="$t('SalesVoucherList.customer')"
                label-for="customer"
                class="col-12 col-sm-4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="customer"
                  rules="required"
                >
                  <v-select
                    id="customer"
                    label="full_name"
                    v-model="showData.customer_id"
                    :options="customerOption"
                    :placeholder="$t('SalesVoucherList.customer')"
                    :reduce="option => option.id"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
  
               <!--  -->
               <b-form-group
                :label="$t('SalesVoucherList.currency')"
                label-for="currency"
                class="col-12 col-sm-4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="currency"
                  rules="required"
                >
                  <v-select
                    id="currency"
                    label="name"
                    v-model="showData.currency_id"
                    :options="currencyOption"
                    :placeholder="$t('SalesVoucherModal.selectCurrency')"
                    :reduce="option => option.id"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
  
              
  
              <!--  -->
              <b-form-group
                :label="$t('SalesVoucherList.transferNo')"
                label-for="transferNo"
                class="col-12 col-sm-4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="transferNo"
                >
                  <v-select
                    label="transferNo"
                    v-model="showData.transferNo_id"
                    :options="transferNoOption"
                    :placeholder="$t('SalesVoucherModal.selectTransferNo')"
                    :reduce="option => option.id"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
  
        
  
              <!--  -->
              <b-form-group
                :label="$t('SalesVoucherList.businessPeople')"
                label-for="businessPeople"
                class="col-12 col-sm-4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="businessPeople"
                >
                  <v-select
                    id="businessPeople"
                    label="name"
                    v-model="showData.businessPeople_id"
                    :options="staffOption"
                    :placeholder="$t('SalesVoucherModal.businessPeople')"
                    :reduce="option => option.id"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
  
              <!--  -->
              <b-form-group
                :label="$t('SalesVoucherList.quotationdepartment')"
                label-for="quotationdepartment"
                class="col-12 col-sm-4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="quotationdepartment"
                >
                  <v-select
                    id="quotationdepartment"
                    label="name"
                    v-model="showData.quotationDepartment_id"
                    :options="quotationdepartmentOption"
                    :placeholder="$t('SalesVoucherModal.quotationdepartment')"
                    :reduce="option => option.id"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
  
               <!--  -->
               <b-form-group
                :label="$t('SalesVoucherList.quotationEffectiveDate')"
                label-for="quotationEffectiveDate"
                class="col-12 col-sm-4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="effective_date"
                  rules="required"
                >
                  <flat-pickr
                    id="quotationEffectiveDate"
                    v-model="showData.effective_date"
                    class="form-control"
                    :placeholder="$t('SalesVoucherList.quotationEffectiveDate')"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
  
               <!--  -->
               <b-form-group
                :label="$t('SalesVoucherList.quotationExpirationDate')"
                label-for="quotationExpirationDate"
                class="col-12 col-sm-4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="quotationExpirationDate"
                >
                  <flat-pickr
                    id="quotationExpirationDate"
                    v-model="showData.expiration_date"
                    class="form-control"
                    :placeholder="$t('SalesVoucherList.quotationExpirationDate')"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
  
  
              <!--  -->
              <b-form-group
                :label="$t('SalesVoucherList.taxDeductionCategory')"
                label-for="taxDeductionCategory"
                class="col-12 col-sm-4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="taxDeductionCategory"
                >
                  <v-select
                    id="taxDeductionCategory"
                    label="label"
                    v-model="showData.tax_type"
                    :options="[
                      { label: '', value: 'taxFree' },
                      { label: '', value: 'taxableIncluded' },
                      { label: '', value: 'taxablePlus' },
                    ]"
                    :placeholder="$t('SalesVoucherList.selectTaxDeductionCategory')"
                    :reduce="option => option.value"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
  
  
  
               <!--  -->
               <b-form-group
                :label="$t('SalesVoucherList.projectNo')"
                label-for="projectNo"
                class="col-12 col-sm-4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="projectNo"
                >
                  <b-form-input
                    id="projectNo"
                    v-model="showData.project_id"
                    type="text"
                    :placeholder="$t('SalesVoucherList.projectNo')"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
  
                <!--  -->
                <b-form-group
                :label="$t('SalesVoucherList.project')"
                label-for="project"
                class="col-12 col-sm-4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="projectName"
                  rules="required"
                >
                  <b-form-input
                    id="project"
                    v-model="showData.project_naem"
                    type="text"
                    :placeholder="$t('SalesVoucherList.project')"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
  
              
  
              <!--  -->
              <b-form-group
                :label="$t('SalesVoucherList.customerReservation')"
                label-for="customerReservation"
                class="col-12 col-sm-4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="order"
                >
                  <b-form-input
                    id="customerReservation"
                    v-model="showData.customerReservation"
                    type="text"
                    :placeholder="$t('SalesVoucherList.customerReservation')"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
  
              
  
              <!--  -->
              <b-form-group
                :label="$t('SalesVoucherList.untaxedLocalCurrency')"
                label-for="untaxedAmount"
                class="col-12 col-sm-4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="untaxedLocalCurrency"
                >
                  <b-form-input
                    id="untaxedLocalCurrency"
                    v-model="showData.untaxedlocalcurrency"
                    type="text"
                    :placeholder="$t('SalesVoucherList.untaxedLocalCurrency')"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
  
              <!--  -->
              <b-form-group
                :label="$t('SalesVoucherList.localCurrencyTax')"
                label-for="localCurrencyTax"
                class="col-12 col-sm-4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="localCurrencyTax"
                >
                  <b-form-input
                    id="localCurrencyTax"
                    v-model="showData.localcurrencytax"
                    type="text"
                    :placeholder="$t('SalesVoucherList.localCurrencyTax')"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
  
              <!--  -->
              <b-form-group
                :label="$t('SalesVoucherList.totalLocalCurrency')"
                label-for="totalLocalCurrency"
                class="col-12 col-sm-4"
              >
                <validation-provider
                  #default="{ errors }"
                  name="totalLocalCurrency"
                >
                  <b-form-input
                    id="totalLocalCurrency"
                    v-model="showData.totallocalcurrency"
                    type="text"
                    :placeholder="$t('SalesVoucherList.totalLocalCurrency')"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
  
              <!--  -->
              <b-form-group
                :label="$t('remark')"
                label-for="remark"
                class="col-12"
              >
                <validation-provider
                  #default="{ errors }"
                  name="remark"
                >
                  <b-form-textarea
                    id="remark"
                    v-model="showData.remark"
                    type="text"
                    :placeholder="$t('remark')"
                    rows="5"
                  />
                  <small class="text-danger">{{ errors[0] }}</small>
                </validation-provider>
              </b-form-group>
            </b-row>
          </b-card>
  
          <!--  -->
          <b-card
            v-if="showData"
            :title="$t('SalesVoucherModal.productInformation')"
          >
            <b-button
              variant="primary"
              type="button"
              size="sm"
              class="mb-1"
              @click.prevent="addItem(showData.items)"
            >
              <feather-icon icon="PlusIcon" />
            </b-button>
  
            <b-table-simple
              responsive
              bordered
              style="overflow-x: visible"
            >
              <b-thead>
                <b-tr>
                  <b-th class="text-nowrap">#</b-th>
                  <b-th class="text-nowrap"> {{$t('SalesVoucherList.ProductList.productNo') }} </b-th>
                  <b-th class="text-nowrap"> {{$t('SalesVoucherList.ProductList.productName') }} </b-th>
                  <b-th class="text-nowrap"> {{$t('SalesVoucherList.ProductList.specification') }} </b-th>
                  <b-th class="text-nowrap"> {{$t('SalesVoucherList.ProductList.unit') }} </b-th>
                  <b-th class="text-nowrap"> {{$t('SalesVoucherList.ProductList.storehouse') }} </b-th>
                  <b-th class="text-nowrap"> {{$t('SalesVoucherList.ProductList.quantity') }} </b-th>
                  <b-th class="text-nowrap"> {{$t('SalesVoucherList.ProductList.unitPrice') }} </b-th>
                  <b-th class="text-nowrap"> {{$t('SalesVoucherList.ProductList.amount') }} </b-th>
                  <b-th class="text-nowrap"> {{$t('SalesVoucherList.ProductList.numberOfTransfers') }} </b-th>
                  <b-th class="text-nowrap"> {{$t('SalesVoucherList.ProductList.untransferredQuantity') }} </b-th>
                  <b-th class="text-nowrap"> {{$t('SalesVoucherList.ProductList.productRemark') }} </b-th>
                  <b-th class="text-nowrap"> {{$t('SalesVoucherList.ProductList.action') }} </b-th>
                </b-tr>
              </b-thead>
  
              <b-tbody>
                <!-- items -->
                <b-tr
                  v-for="(item, index) in showData.items"
                  :key="index"
                >
                  <b-th>{{ index + 1 }}</b-th>
                  <!--  -->
                  <b-td>
                    <b-form-input
                      v-model="item.code"
                      disabled
                    />
                  </b-td>
                  <!--  -->
                  <b-td>
                    <v-select
                      :id="`product-` + index"
                      label="name"
                      v-model="item.product_id"
                      :options="productOption"
                      :placeholder="$t('SalesVoucherModal.selectProductName')"
                      :filterable="false"
                      style="width: 300px;"
                      @search="onSearchProduct"
                      @input="val => selectProduct(val, item)"
                    >
                      <template slot="no-options">
                         ...
                      </template>
                    </v-select>
                  </b-td>
                  <!--  -->
                  <b-td>
                    <b-form-input
                      v-model="item.sku"
                      disabled
                    />
                  </b-td>
                  <!--  -->
                  <b-td>
                    <b-form-input
                      v-model="item.unit"
                      disabled
                    />
                  </b-td>
                  <!--  -->
                  <b-td>
                    <v-select
                      :id="`storehouse-` + index"
                      label="name"
                      v-model="item.storehouse_id"
                      :options="storehouseOption"
                      :placeholder="$t('SalesVoucherModal.selectStorehouse')"
                      :reduce="option => option.id"
                      class="text-nowrap"
                      style="width: 120px;"
                    />
                  </b-td>
                  <!--  -->
                  <b-td>
                    <validation-provider
                      #default="{ errors }"
                      name="quantity"
                      rules="required"
                    >
                      <b-form-input
                        type="number"
                        v-model="item.quantity"
                        min="0"
                        :placeholder="$t('SalesVoucherList.ProductList.quantity')"
                      />
                      <small class="text-danger">{{ errors[0] }}</small>
                    </validation-provider>
                  </b-td>
                  <!--  -->
                  <b-td>
                    <validation-provider
                      #default="{ errors }"
                      name="unitPrice"
                      rules="required"
                    >
                      <b-form-input
                        type="number"
                        v-model="item.price"
                        min="0"
                        :placeholder="$t('SalesVoucherList.ProductList.unitPrice')"
                      />
                      <small class="text-danger">{{ errors[0] }}</small>
                    </validation-provider>
                  </b-td>
                  <!--  -->
                  <b-td>
                    <b-form-input
                      type="number"
                      v-model="item.amount"
                      min="0"
                    />
                  </b-td>
                  <!--  -->
                  <b-td>
                    <b-form-input
                     v-model="item.numberOfTransfers"   
                    disabled />
                  </b-td>
                  <!--  -->
                  <b-td>
                    <b-form-input 
                    v-model="item.untransferredQuantity" 
                    disabled
                    />
                  </b-td>
                  <!--  -->
                  <b-td>
                    <b-form-input v-model="item.remark" />
                  </b-td>
                  <b-td>
                    <b-button
                      class="ml-50"
                      size="sm"
                      variant="gradient-danger"
                      @click="removeItem(showData.items, index)"
                    >
                      <feather-icon
                        icon="Trash2Icon"
                        size="12"
                      />
                    </b-button>
                  </b-td>
                </b-tr>
              </b-tbody>
         
            </b-table-simple>
          </b-card>
          <!--  -->
          <div class="d-sm-flex align-items-center justify-content-center">
            <b-button
              variant="outline-danger"
              type="button"
              :block="$store.getters['app/currentBreakPoint'] === 'xs'"
              class="mb-1 mb-sm-0 mr-0 mr-sm-1"
              :to="{ name: 'SalesOperation-SalesVoucherList' }"
            >
              <feather-icon
                icon="ArrowLeftIcon"
                class="mr-50"
              />
              {{ $t('back') }}
            </b-button>
  
            <b-button
              variant="primary"
              type="submit"
              :block="$store.getters['app/currentBreakPoint'] === 'xs'"
              @click.prevent="validationForm"
            >
              <feather-icon
                icon="SaveIcon"
                class="mr-50"
              />
              {{ $t('Submit') }}
            </b-button>
          </div>
        </validation-observer>
      </b-form>
  
    </div>
  </template>
  
  <script>
  import { ValidationProvider, ValidationObserver } from 'vee-validate'
  import { required } from '@validations'
  import {
    BCard, BTabs, BTab, BRow, BCol,
    BForm, BFormGroup, BFormInput, BFormTextarea,
    BTableSimple, BThead, BTbody, BTfoot, BTh, BTr, BTd,
    BButton,
  } from "bootstrap-vue"
  import { ref } from "@vue/composition-api"
  import vSelect from 'vue-select'
  import flatPickr from 'vue-flatpickr-component'
  import { VueGoodTable } from 'vue-good-table'
  import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
  import axios from "@axios"
  
  export default {
  
    components: {
      ValidationProvider,
      ValidationObserver,
  
      BCard,
      BTabs,
      BTab,
      BRow,
      BCol,
  
      BForm,
      BFormGroup,
      BFormInput,
      BFormTextarea,
  
      BTableSimple,
      BThead,
      BTbody,
      BTfoot,
      BTh,
      BTr,
      BTd,
  
      BButton,
  
      vSelect,
      flatPickr,
      VueGoodTable,
    },
    setup(_, { root, refs }) {
      const showData = ref(null)
  
      // 
      if (root.$route.query.id) {
        axios.get(`/requisitions-orders/${root.$route.query.id}`)
          .then(response => {
            const data = response.data
            showData.value = JSON.parse(JSON.stringify(data))
          })
      } else {
        showData.value = {
          items: [],
        }
      }
  
      // 
      const validationForm = () => {
        refs.simpleRules.validate()
          .then(success => {
            if (!success) return
  
            if (showData.value.id) {
              axios
                .put(`/requisitions-orders/${showData.value.id}`, showData.value)
                .then(() => {
                  this.$toast({
                    component: ToastificationContent,
                    position: 'top-right',
                    props: {
                      title: `${this.$t('updatedSuccess')}`,
                      icon: 'CoffeeIcon',
                      variant: 'success',
                      text: `${root.$t('Procurement Voucher')} ${root.$t('updatedSuccess')}!`,
                    },
                  })
                  root.$router.push({ name: 'SalesOperation-SalesVoucherList' });
                })
                .catch(error => {
                  root.$toast({
                    component: ToastificationContent,
                    position: 'top-right',
                    props: {
                      title: `${root.$t('updatedFailed')}`,
                      icon: 'XIcon',
                      variant: 'danger',
                      text: error.response.data.message,
                    },
                  })
                })
  
            } else {
              axios
                .post('requisitions-orders', showData.value)
                .then(() => {
                  this.$toast({
                    component: ToastificationContent,
                    position: 'top-right',
                    props: {
                      title: `${root.$t('createdSuccess')}`,
                      icon: 'CoffeeIcon',
                      variant: 'success',
                      text: `${root.$t('Procurement Voucher')} ${root.$t('createdSuccess')}!`,
                    },
                  })
                  root.$router.push({ name: 'SalesOperation-SalesVoucherList' });
                })
                .catch(error => {
                  root.$toast({
                    component: ToastificationContent,
                    position: 'top-right',
                    props: {
                      title: `${root.$t('createdFailed')}`,
                      icon: 'XIcon',
                      variant: 'danger',
                      text: error.response.data.message,
                    },
                  })
                })
            }
          })
      }
  
      // 
      const addItem = items => {
        items.push({})
      }
  
      // 
      const removeItem = (items, index) => {
        root.$swal({
          title: `${root.$t('checkDelete')}`,
          text: `${root.$t('cantRevert')}`,
          icon: 'warning',
          showCancelButton: true,
          confirmButtonText: `${root.$t('yes')}`,
          cancelButtonText: `${root.$t('no')}`,
          customClass: {
            confirmButton: 'btn btn-primary',
            cancelButton: 'btn btn-outline-danger ml-1',
          },
          buttonsStyling: false,
        }).then(result => {
          if (result.value) {
            items.splice(index, 1)
          }
        })
      }
  
      // 
      const transferNoOption = ref([])
  
      // 
      const currencyOption = ref([])
      axios.post('/currencies/options')
        .then(response => {
          currencyOption.value = response.data
        })
  
      // 
      const projectOption = ref([])
      axios.post('/projects/options')
        .then(response => {
          projectOption.value = response.data
        })
  
      // 
      const quotationdepartmentOption = ref([])
      axios.post('/departments/options')
        .then(response => {
          quotationdepartmentOption.value = response.data
        })
  
      // 
      const staffOption = ref([])
      axios.post('/staffs/options')
        .then(response => {
          staffOption.value = response.data
        })
  
      // 
      const customerOption = ref([])
      axios.post('/customer-manufacturers/options')
        .then(response => {
          customerOption.value = response.data
        })
  
      // 
      const storehouseOption = ref([])
      axios.post('/storehouses/options')
        .then(response => {
          storehouseOption.value = response.data
        })
  
      // 
      const productOption = ref([])
      const onSearchProduct = (search, loading) => {
        if (search.length) {
          loading(true);
  
          axios.post('/products/options', { searchTerm: escape(search) })
            .then(response => {
              productOption.value = response.data
              loading(false)
            })
        }
      }
  
      // 
      const selectProduct = (select, item) => {
        if (select) {
          root.$set(item, 'id', select.id);
          root.$set(item, 'code', select.code);
          root.$set(item, 'sku', select.sku);
          root.$set(item, 'unit', select.unit);
        }
      }
  
      return {
        showData,
  
        validationForm,
  
        addItem,
        removeItem,
  
        transferNoOption,
        currencyOption,
        projectOption,
        quotationdepartmentOption,
        staffOption,
        customerOption,
        storehouseOption,
        productOption,
  
        onSearchProduct,
        selectProduct,
  
        // 
        required,
      }
    },
  }
  </script>
  
  <style lang="scss" >
  tbody {
    tr {
      td {
        padding: 0.5rem !important;
      }
    }
  }
  
  @media (max-width: 767px) {
    .table-responsive .dropdown-menu {
      position: static !important;
    }
  }
  @media (min-width: 768px) {
    .table-responsive {
      overflow-y: visible;
    }
  }
  
  @import "@core/scss/vue/libs/vue-select.scss";
  @import "@core/scss/vue/libs/vue-flatpicker.scss";
  </style>
  